.mainBlock {
    height: 100vh;
    background-color: #f8a5a5;
}

.text {
    width: 350px;
    background-color: #9a9a9a;
    padding: 10px;
}

.photo {
    width: 350px;
    height: 400px;
    background-color: #9a9a9a;
}

@media screen and (max-width: 480px) {
    .mainBlock {
        height: auto;
    }

    .photo, .text {
        width: 250px;
    }
}