.skillBlock {
    width: 250px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 10px;
}



.icon {
    width: 60px;
    height: 60px;
    background-color: #9a9a9a;
}

.skillTitle {
    padding: 10px;
    background-color: #74cbf5;
}

.skillDescription {
    padding: 10px;
    background-color: #9a9a9a;
}
